nodeClick<template>
  <div>
    <v-contextmenu ref="treeMenu">
      <v-contextmenu-item @click="createShelve">新增</v-contextmenu-item>
      <v-contextmenu-item @click="editShelve">编辑</v-contextmenu-item>
      <v-contextmenu-item @click="deleteShelve">删除</v-contextmenu-item>
      <v-contextmenu-item @click="createMore">多库位生成</v-contextmenu-item>
    </v-contextmenu>
    <el-tree
      ref="tree"
      :data="shelveTreeData"
      node-key="id"
      :props="defaultProps"
      :default-expanded-keys="expendKey"
      :expand-on-click-node="false"
      @node-contextmenu="showContextMenu"
      @node-click="handleNodeClick"
      :render-content="customRender"
    >
    </el-tree>
  </div>
</template>

<script>
  import { mapGetters } from 'vuex'
  import { baseTheme } from '@/utils/theme'

export default {
  name: 'ShelvesTree',
  data () {
    return {
      warehouseList: [],
      defaultProps: {
        label: 'name'
      },
      contextmenuSelectedNode: null,
      expendKey: []
    }
  },
  props: {
    clickedNodeData: {
      type: Object
    },
    selectedId: {
      type: String
    },
    shelveTreeData:{
      type: Array
    }
  },
  computed: {
    ...mapGetters(['systemTheme']),
    selectedNode () {
      return this.clickedNodeData
    },
    systemColor () {
      return baseTheme[this.systemTheme]
    }
  },
  watch: {
    shelveTreeData (newVal) {
      if(newVal.length){
        this.expendKey = [newVal[0].id]
      }
    }
  },
  methods: {
    // 节点右键弹出菜单
    showContextMenu (event, data, node, nodeCom) {
      if (node) {
        const tree = this.$refs.tree
        const contextMenu = this.$refs.treeMenu
        // 右键设置当前树节点为选中状态
        this.contextmenuSelectedNode = data
        tree.setCurrentKey(data.id)
        // 弹出菜单
        contextMenu.show({top: event.clientY, left: event.clientX})
      }
    },
    createShelve () {
      this.$emit('update:isCreate', true)
      this.$emit('openDialog', this.contextmenuSelectedNode)
    },
    editShelve () {
      this.$emit('update:isCreate', false)
      this.$emit('openDialog', this.contextmenuSelectedNode)
    },
    deleteShelve () {
      this.$emit('deleteNode',this.contextmenuSelectedNode)
    },
    // 树节点自定义渲染格式 (JSX)
    customRender (createElement, {node, data, store}) {
      // directive 参数参考指令钩子函数中的定义: https://cn.vuejs.org/v2/guide/custom-directive.html
      const directives = [{name: 'contextmenu', arg: 'treeMenu'}]
      return (
        <span class="custom-tree-node" { ...{directives} } >
    <span>{node.label}</span>
      {
        (this.$refs.tree.getCurrentNode() && data.id === this.$refs.tree.getCurrentNode().id) &&
        <span class="active" style={{backgroundColor: this.systemColor}}></span>
      }
    </span>
    )
    },
    // 节点点击事件
    handleNodeClick (data) {
      this.$emit('nodeClick', data)
    },
    createMore () {
      this.$emit('openCreateMoreDialog')
    }
  }
}
</script>

<style lang="scss" scoped>
  .custom-tree-node {
    position:relative;
    width:100%;

  .active {
    position: absolute;
    display: inline-block;
    width:10px;
    height:10px;
    right:10px;
    top:3px;
    border-radius:10px;
  }
  }
</style>



